import React from 'react';
import {Element} from 'react-scroll';

import FourStepsColumn from '../FourStepsColumn/FourStepsColumn';
import Btn from '../Btn/Btn';
import DecorationText from "../DecorationText/DecorationText";

import './FourSteps.scss';
import {withRouter} from "react-router-dom";

class FourSteps extends React.Component {

    createAccount = () => {
        this.props.history.push('/register');
    };

    render() {

        const columnsData = [];
        columnsData.push({iconNumber:'1', title:'Wybierz rzeczy', description:'ubrania, zabawki, sprzt i inne'});
        columnsData.push({iconNumber:'2', title:'Spakuj je', description:'skorzystaj z workw na mieci'});
        columnsData.push({iconNumber:'3', title:'Zdecyduj komu chcesz pomc', description:'wybierz zaufane miejsce'});
        columnsData.push({iconNumber:'4', title:'Zamw kuriera', description:'kurier przyjedzie w dogodnym terminie'});

        const fourStepsColumns = columnsData.map((column) => {
            let {iconNumber, title, description} = column;
            return <FourStepsColumn key={title} iconNumber={iconNumber} title={title} description={description}/>;
        });

        return (
            <Element name='fourSteps'>
            <section className='fourSteps'>
                <DecorationText texts={['Wystarcz 4 proste kroki']}/>
                <div className='fourStepsCenter'>
                    {fourStepsColumns}
                </div>
                {!this.props.logged &&
                <div className='fourSteps__btn-container'>
                    < Btn goTo={this.createAccount} text='ZA KONTO' specificWidth='21vw'/>
                </div>
                }
            </section>
            </Element>
        );
    }
}

export default withRouter(FourSteps);